import React, { Component } from 'react';
import { Grid, Message, Form, Button, Input} from 'semantic-ui-react'
import { render } from 'react-dom';
import $ from 'jquery';
import DriverChangePassword from './change_password';
var validator = require('validator');

class DriverChangePasswordVarify extends Component {
    constructor(){
      super();
      this.state = {
        varificationCode : ''
      }
    }

    validateVarification = () => {
        let errors = [];
        
        if(this.state.varificationCode.length === 0) {
            errors.push("   !");
        } else if (validator.isLength(this.state.varificationCode, {min: 5}) === false){
            errors.push(" 5     !");
        }else if(validator.isLength(this.state.varificationCode, {max: 5}) === false){
            errors.push(" 5     !");
        } else if (validator.isNumeric(this.state.varificationCode, {no_symbols: true} ) === false) {
            errors.push("   !");
        }
    
        return errors;
    }

    getErrorList(errors){
        var i = 0;
        let error_list = errors.map(error => {
            return <li key={i++}>{error}</li>
        });
        return error_list;
    }

    change = (e) => {
        this.setState({
            [e.target.name]: e.target.value
        })
    } 

    onVarify = (e) => {
        e.preventDefault();
        const err = this.validateVarification();
        if(err.length > 0){
            let error_list = this.getErrorList(err);
            render(<Message negative>{error_list}</Message>,document.getElementById('div-rply'));
        } else {
            var data = {
                varification_code : this.state.varificationCode
            }
            this.varify(data)
        }
    }

    varify = (data) => {
        $('.btn_mobile_varify').addClass("loading");
        $.ajax({ 
            type:"POST",
            url:"/driver/change_password_verification",
            headers: { 'x-auth': sessionStorage.getItem("_change_password_mobile")},
            data: JSON.stringify(data), 
            contentType: "application/json",
            success: function(data, textStatus, jqXHR) {
               $('.btn_mobile_varify').removeClass("loading");
               if(data.rply === 1){
                sessionStorage.setItem("_change_password_varificationCode", this.state.varificationCode);
                render(<DriverChangePassword></DriverChangePassword>,document.getElementById('div-change-pass'));
               } 
               this.setState({
                varificationCode : '',
                errors: []
               });
            }.bind(this),
            error: function(xhr, status, err) {
                $('.btn_mobile_varify').removeClass("loading");
                render(<Message negative>   !</Message>,document.getElementById('div-rply'));
                console.error(xhr, status, err.toString());
                this.setState({
                    varificationCode : '',
                    errors: []
                });
            }.bind(this)
        });  
    }


  //I WORSHIP YOU LORD -         
  render(){
      return(
        <div>
            <Grid columns={1}>
             <Grid.Row columns={1}>
                    <Grid.Column mobile={18} tablet={18} computer={18}>
                        <Form>
                        <Input
                        name='varificationCode'
                        type='text'
                        label={{ icon: 'mail', color: 'blue' }} 
                        labelPosition="left corner"
                        value={this.state.varificationCode}
                        placeholder='5  '
                        onChange={e => this.change(e)}
                        size='huge'
                        fluid
                        />
                        </Form>
                    </Grid.Column>
             </Grid.Row>
             <Grid.Row>
                    <Grid.Column mobile={18} tablet={18} computer={18}>
                     <Button className="btn_mobile_varify" content='' icon='right arrow' color="green" labelPosition='right' size='huge' onClick={(e) => this.onVarify(e)}  fluid />
                    </Grid.Column> 
             </Grid.Row>
             <Grid.Row>
                    <Grid.Column mobile={18} tablet={18} computer={18}>
                    <div id='div-rply' className='div-rply'>
                    <Message positive >
                              
                          
                    </Message>
                    </div>
                    </Grid.Column> 
             </Grid.Row>
          </Grid>  
        </div>
      )
  }
}
export default DriverChangePasswordVarify;